
#app {
   height:100%;
  width:100%;
  overflow: hidden; 
  margin: 0px;
}
p {
  font-size: 25px;
  color: black;
  margin-top: 10px;
  margin-left: 2%;
}
select {
  width: 60%;
  height: 25%;
  color:black;
}
.search-icon {
  width: 70%;
  height: 50%;
  background-image: url(./../assets/searchEmail.png);
  background-size: 15% 100%;
  background-repeat: no-repeat;
  float: left;
  margin-top: 5px;
  margin-left: 250px;
}
.iu-top {
  height:50%;
  width:100%;
  box-sizing: border-box;
  float: left;
}
.iu-bottom {
  height:50%;
  width:100%;
  box-sizing: border-box;
  float: left;
  margin-top: 50px;
}
.iu-add-friends{
  width: 40%;
  height: 30%;
  margin-top: 10%;
  margin-left: 30%;
  margin-bottom: 10%;
}
.iu-emailFriend {
  width: 80%;
  height: 40%;
  margin-left: 10%;
  margin-top: 5%;
  box-sizing: border-box;
}
.iu-add {
  margin-top: 35px;
  margin-left: 12px;
  width: 20%;
  height: 40%;
  border-radius: 30px;
  background-color: #EE9C00;
  font-size: 15px;
  color: #F1F2F1;
}
.iu-add:hover {
  cursor: pointer;
  background-color: #CD6427 ;
  color: #F1F2F1;
}
.n-go-back {
  float: left;
  width: 10%;
  height: 5%;
  position: absolute;
  left: 15%;
  top: 5%;
}
.n-go-back:hover {
  cursor: pointer;
}
.go-back-icon {
  width: 20%;
  height: 100%;
  float: left;
  background-image: url(./../assets/goBack.png);
  background-size: 80%;
  background-repeat: no-repeat;
}
.go-back-text {
  width: 60%;
  height: 100%;
  float: left;
  color: #EE9C00;
  font-size: 20px;
  text-align: left;
}



