
#app {
  height:100%;
  width:100%;
  overflow: hidden; 
  margin: 0px;
}
.notification-header {
  width: 100%;
  height: 10%;
  color: #EE9C00;
  font-size: 25px;
  padding-top: 20px;
}
.n-go-back {
  float: left;
  width: 10%;
  height: 5%;
  position: absolute;
  left: 15%;
  top: 5%;
}
.n-go-back:hover {
  cursor: pointer;
}
.go-back-icon {
  width: 20%;
  height: 100%;
  float: left;
  background-image: url(./../assets/goBack.png);
  background-size: 80%;
  background-repeat: no-repeat;
}
.go-back-text {
  width: 60%;
  height: 100%;
  float: left;
  color: #EE9C00;
  font-size: 20px;
  text-align: left;
}



